.card-placeholder {
  height: var(--mdx-comp-carousel-sizing-placeholder-height);
  width: var(--mdx-comp-carousel-sizing-placeholder-width);

  &__content {
    height: 100%;
    background-color: var(--mdx-comp-carousel-placeholder-background-color-default);
    border-radius: 2rem;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
